* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.coutry-list__item {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 10px 0;

}

.country-list__descr {
  display: inline-flex;
  margin: 10px 20px;
}

.subscription {
  display: flex;
  align-items: center;
}

.country-info__descr {
font-weight: bold;
}

.country-info__value {
font-size: 20px;
font-weight: normal;
margin-left: 10px;

}

.country-info__name {
  margin: 0 20px;
}

